import LineChart from '@/components/LineChart/LineChart';
import DateRangePicker from '@/components/DateRangePicker/DateRangePicker';
import request from '@/utils/request';
import autotime from '@/utils/autotime';

var moment = require('moment');

export default {
  components: {
    DateRangePicker: DateRangePicker,
    LineChart: LineChart
  },
  data: function data() {
    return {
      config: [{
        index: 'pv',
        title: '浏览量',
        value: 0,
        fiss: 0,
        hb_fiss: 0
      }, {
        index: 'uv',
        title: '访客数',
        value: 0,
        fiss: 0,
        hb_fiss: 0
      }, {
        index: 'pv_pay',
        title: '支付订单',
        value: 0,
        fiss: 0,
        hb_fiss: 0
      }, {
        index: 'uv_pay',
        title: '支付人数',
        value: 0,
        fiss: 0,
        hb_fiss: 0
      }, {
        index: 'gmv_fee',
        title: '支付金额',
        value: 0,
        fiss: 0,
        hb_fiss: 0
      }, {
        index: 'uv_share',
        title: '分享人数',
        value: 0,
        fiss: 0,
        hb_fiss: 0
      }, {
        index: 'pv_share',
        title: '分享次数',
        value: 0,
        fiss: 0,
        hb_fiss: 0
      }],
      currentId: 0,
      usergroup: 'all',
      srcgroup: 'all',
      lineChartData: {
        tit: '',
        actualData: [],
        xdate: [],
        date: '浏览量'
      },
      dates: [],
      lineChartArr: {},
      loading: false
    };
  },
  created: function created() {
    autotime.reloadTime();
    this.dates = [autotime.y7day, autotime.yday];
    this.refreshCharts();
  },
  methods: {
    refreshCharts: function refreshCharts() {
      var _this = this;

      var group = this.usergroup;

      if (this.srcgroup !== 'all') {
        if (this.usergroup === 'all') {
          group = this.srcgroup;
        } else {
          group = this.srcgroup + '_' + group;
        }
      }

      var mts = moment(this.dates[0]);
      var mte = moment(this.dates[1]);
      this.loading = true;
      request({
        url: '/app/business/appIndex',
        method: 'post',
        data: {
          gid: group,
          indices: ['pv', 'uv', 'pv_pay', 'uv_pay', 'gmv_fee', 'pv_share', 'uv_share', 'pv_fiss', 'uv_fiss', 'pv_pay_fiss', 'uv_pay_fiss', 'gmv_fee_fiss', 'pv_share_fiss', 'uv_share_fiss'],
          sts: mts.unix(),
          ets: mte.unix() + 86400
        }
      }).then(function (res) {
        _this.loading = false;

        _this.config.forEach(function (v, i) {
          var idx = _this.config[i].index;
          _this.config[i].value = Math.round(res.data[idx] || 0);
          _this.config[i].fiss = Math.round(res.data[idx + '_fiss'] || 0);
        });

        _this.getChartData(group, mts, mte);
      }).catch(function (e) {
        _this.loading = false;
      });
    },
    getChartData: function getChartData(group, mts, mte) {
      var _this2 = this;

      var data = {
        gid: group,
        indices: ['pv', 'uv', 'pv_pay', 'uv_pay', 'gmv_fee', 'pv_share', 'uv_share'],
        sts: mts.unix(),
        ets: mte.unix() + 86400
      };

      if (mts.isSame(mte)) {
        data.time_group = 'hh';
        this.lineChartData.xdate = autotime.hours;
      } else if (mts.isBefore(mte)) {
        data.time_group = 'ds';
        this.lineChartData.xdate = [];

        while (!mts.isAfter(mte)) {
          this.lineChartData.xdate.push(mts.format('YYYY-MM-DD'));
          mts.add(1, 'd');
        }
      } else {
        return;
      }

      this.loading = true;
      request({
        url: '/app/business/appIndex',
        method: 'post',
        data: data
      }).then(function (res) {
        _this2.loading = false;

        if (data.time_group === 'hh') {
          _this2.lineChartArr = {};
          var result = res.data[mts.format('YYYY-MM-DD')];
          data.indices.forEach(function (v) {
            _this2.lineChartArr[v] = [];

            _this2.lineChartData.xdate.forEach(function (h, i) {
              _this2.lineChartArr[v].push(result && result[i.toString()] && result[i.toString()][v] || 0);
            });
          });
        } else {
          _this2.lineChartArr = {};
          data.indices.forEach(function (v) {
            _this2.lineChartArr[v] = [];

            _this2.lineChartData.xdate.forEach(function (i) {
              _this2.lineChartArr[v].push(res.data[i] && res.data[i][v] || 0);
            });
          });
        }

        _this2.choose(_this2.currentId);
      }).catch(function (e) {
        _this2.loading = false;
      });
    },
    choose: function choose(i) {
      this.currentId = i;
      this.lineChartData.date = this.config[i].title;
      this.lineChartData.actualData = this.lineChartArr[this.config[i].index];
    }
  }
};